import { useForm } from 'react-hook-form';
import {
  Btn,
  BtnsWrapper,
  CancelBtn,
  Form,
  // ImgPreview,
  ItemWrapper,
  LoadBtn,
  Tip,
} from './AdminProductEdit.styled';
import { useEffect, useState } from 'react';
import * as API from 'services/api';
import toast from 'react-hot-toast';

export function AdminProductEdit({ product = {}, onCancel, action }) {
  const {
    name,
    code,
    availability,
    characteristics = {},
    countries,
    description,
    price,
    roastLevel,
    category,
  } = product;

  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState(category?._id);
  const [selectedAvailability, setSelectedAvailability] =
    useState(availability);
  const [file, setFile] = useState(null);

  // const imagePath = `http://127.0.0.1:1880/images/${product._id}`;

  const {
    register,
    handleSubmit,
    formState: { errors },
    setValue,
    reset,
  } = useForm();

  const onFileChange = e => {
    if (e.target.files) {
      setFile(e.target.files[0]);
    }
  };

  const onSubmit = async data => {
    if (action === 'edit') {
      try {
        await API.updateProduct(product._id, data);
        toast.success('  ');

        console.log(data);
      } catch (error) {
        console.error(error);
        toast.error('   ,  ');
      }
    }

    if (action === 'create') {
      if (data.image === '' || typeof data.image === 'object') {
        toast.error(' ');
      } else {
        try {
          await API.createProduct(data);
          toast.success('  ');
          reset();
        } catch (error) {
          console.error(error);
          toast.error('   ,  ');
        }
      }
    }
  };

  const uploadImage = async () => {
    try {
      const formData = new FormData();
      formData.append('image', file, file.name);
      formData.append('name', file.name);
      setValue('image', file.name);
      formData.append('size', file.size);

      await API.uploadImage(formData, file);
      toast.success('  ');
      setFile(null);
    } catch (error) {
      console.error(error);
      toast.error(' ');
    }
  };

  useEffect(() => {
    (async function getCategories() {
      try {
        const categories = await API.getCategories();
        setCategories(categories);
      } catch (error) {
        toast.error('  ,  ');
        console.log(error);
      }
    })();
  }, []);

  return (
    <Form
      onSubmit={handleSubmit(onSubmit)}
      action={action}
      encType="multipart/form-data"
    >
      <ItemWrapper>
        <label htmlFor="name"></label>
        <input
          type="text"
          id="name"
          defaultValue={name}
          {...register('name', { required: true })}
        />
        {errors.name && <Tip>  '</Tip>}
      </ItemWrapper>
      <ItemWrapper>
        <label htmlFor="code"> </label>
        <input
          type="text"
          id="code"
          defaultValue={code}
          {...register('code', { required: true, maxLength: 20 })}
        />
        {errors.name && (
          <Tip>  4    ' </Tip>
        )}
      </ItemWrapper>
      <ItemWrapper>
        <label htmlFor="availability"> </label>
        <select
          {...register('availability')}
          value={selectedAvailability}
          onChange={e => setSelectedAvailability(e.target.value)}
        >
          <option value={true}></option>
          <option value={false}></option>
        </select>
      </ItemWrapper>
      <ItemWrapper>
        <label htmlFor="category"></label>
        <select
          {...register('category', { required: true })}
          value={selectedCategory}
          onChange={e => setSelectedCategory(e.target.value)}
        >
          {categories.map(({ name, _id }) => {
            return (
              <option key={_id} value={_id}>
                {name}
              </option>
            );
          })}
        </select>
        {errors.category && <Tip>  '</Tip>}
      </ItemWrapper>
      <ItemWrapper>
        <label htmlFor="sourness"></label>
        <input
          type="number"
          id="sourness"
          defaultValue={characteristics.sourness}
          {...register('sourness', { required: true, min: 1, max: 5 })}
        />
        {errors.sourness && <Tip>    1  5</Tip>}
      </ItemWrapper>
      <ItemWrapper>
        <label htmlFor="bitterness"></label>
        <input
          type="number"
          id="bitterness"
          defaultValue={characteristics.bitterness}
          {...register('bitterness', { required: true, min: 1, max: 5 })}
        />
        {errors.bitterness && <Tip>    1  5</Tip>}
      </ItemWrapper>
      <ItemWrapper>
        <label htmlFor="sturdiness"></label>
        <input
          type="number"
          id="sturdiness"
          defaultValue={characteristics.sturdiness}
          {...register('sturdiness', { required: true, min: 1, max: 5 })}
        />
        {errors.sturdiness && <Tip>    1  5</Tip>}
      </ItemWrapper>
      <ItemWrapper>
        <label htmlFor="saturation"></label>
        <input
          type="number"
          id="saturation"
          defaultValue={characteristics.saturation}
          {...register('saturation', { required: true, min: 1, max: 5 })}
        />
        {errors.saturation && <Tip>    1  5</Tip>}
      </ItemWrapper>
      <ItemWrapper>
        <label htmlFor="countries"> ( )</label>
        <input
          type="text"
          id="countries"
          defaultValue={countries}
          {...register('countries', { required: true })}
        />
        {errors.countries && <Tip>  '</Tip>}
      </ItemWrapper>
      <ItemWrapper>
        <label htmlFor="description"> </label>
        <textarea
          style={{ resize: 'none' }}
          type="text"
          id="description"
          defaultValue={description}
          {...register('description', { required: true })}
        />
        {errors.description && <Tip>  '</Tip>}
      </ItemWrapper>
      <ItemWrapper>
        <label htmlFor="price"> ( 100)</label>
        <input
          type="number"
          id="price"
          defaultValue={price}
          {...register('price', { required: true, min: 0 })}
        />
        {errors.price && (
          <Tip>  '     </Tip>
        )}
      </ItemWrapper>
      <ItemWrapper>
        <label htmlFor="roastLevel"> </label>
        <input
          type="text"
          id="roastLevel"
          defaultValue={roastLevel}
          {...register('roastLevel', { required: true })}
        />
        {errors.roastLevel && <Tip>   ' </Tip>}
      </ItemWrapper>
      {action === 'create' && (
        <ItemWrapper>
          <label htmlFor="image"></label>

          <input
            type="file"
            name="image"
            accept="image/*"
            {...register('image')}
            onChange={onFileChange}
          />

          {errors.image && <Tip>  ' </Tip>}
          <LoadBtn type="button" onClick={uploadImage}>
            
          </LoadBtn>
        </ItemWrapper>
      )}

      <BtnsWrapper>
        <Btn type="submit"></Btn>
        {action === 'edit' && (
          <CancelBtn type="button" onClick={onCancel}>
            
          </CancelBtn>
        )}
      </BtnsWrapper>
    </Form>
  );
}
