import { useForm } from 'react-hook-form';
import {
  Btn,
  BtnsWrapper,
  Form,
  Tip,
  ItemWrapper,
} from 'components/AdminProductEdit/AdminProductEdit.styled';
import { Wrapper } from 'components/AdminProducts/AdminProducts.styled';
import * as API from 'services/api';
import toast from 'react-hot-toast';

export function AdminCreateUser() {
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
    watch,
  } = useForm();

  const onSubmit = async data => {
    try {
      await API.createAdmin(data);
      reset();
      toast.success('  ');
      console.log(data);
    } catch (error) {
      const errorStatus = error.response.status;

      if (errorStatus === 409) {
        toast.error('   ');
      } else {
        toast.error('   , ,  , ');
        console.log(error);
      }
    }
  };

  return (
    <Wrapper>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <ItemWrapper>
          <label htmlFor="name">'*</label>
          <input
            {...register('name', {
              required: "  '",
            })}
            id="name"
            color="auxillary"
          />
          {errors.name && <Tip>  '</Tip>}
        </ItemWrapper>
        <ItemWrapper>
          <label htmlFor="email">*</label>
          <input
            {...register('email', {
              required: "  '",
              pattern: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
            })}
            id="email"
            color="auxillary"
          />
          {errors.email && (
            <Tip>
                     '{' '}
            </Tip>
          )}
        </ItemWrapper>
        <ItemWrapper>
          <label htmlFor="password">*</label>
          <input
            {...register('password', {
              required: "  '",
              minLength: 8,
            })}
            type="password"
            id="password"
            color="auxillary"
          />
          {errors.password && (
            <Tip>
                  8     '
            </Tip>
          )}
        </ItemWrapper>
        <ItemWrapper>
          <label htmlFor="repeatPassword"> *</label>
          <input
            {...register('repeatPassword', {
              required: "  '",
              validate: value => value === watch('password'),
            })}
            id="repeatPassword"
            type="password"
            color="auxillary"
          />
          {errors?.repeatPassword ? (
            errors?.repeatPassword?.type === 'required' ? (
              <Tip>  '</Tip>
            ) : (
              <Tip>  </Tip>
            )
          ) : (
            ''
          )}
        </ItemWrapper>
        <BtnsWrapper>
          <Btn type="submit"></Btn>
        </BtnsWrapper>
      </Form>
    </Wrapper>
  );
}
