import { ThemeProvider, createTheme } from '@mui/material';
import { Button, CloseDeleteBtn, ModalTitle, ModalWrapper } from 'components';
import { InputForm, Label } from 'components';
import { useForm } from 'react-hook-form';
import { colors } from 'styles/utils/variables';
import { Form } from './ModalRegistration.styled';
import * as API from 'services/authApi';
import { toast } from 'react-hot-toast';

const theme = createTheme({
  palette: {
    auxillary: {
      main: colors.auxiliaryText,
    },
  },
});

export function ModalRegistration({ closeModal, onLoginOpen }) {
  const {
    register,
    handleSubmit,
    formState: { errors = {} },
    watch,
  } = useForm();

  const onSubmit = async data => {
    try {
      await API.register(data);
      toast.success('  ');
      closeModal();
      onLoginOpen();
    } catch (error) {
      const errorStatus = error.response.status;

      if (errorStatus === 409) {
        toast.error('   ');
      } else {
        toast.error('   , ,  , ');
        console.log(error);
      }
    }
  };

  return (
    <>
      <ModalWrapper closeModal={closeModal}>
        <ThemeProvider theme={theme}>
          <ModalTitle></ModalTitle>
          <Form onSubmit={handleSubmit(onSubmit)}>
            <CloseDeleteBtn handleClick={closeModal} />
            <Label htmlFor="name">'*</Label>
            <InputForm
              {...register('name', {
                required: "  '",
              })}
              id="name"
              error={!!errors?.name}
              helperText={errors?.name?.message}
              color="auxillary"
            />
            <Label htmlFor="email">*</Label>
            <InputForm
              {...register('email', {
                required: "  '",
                pattern: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
              })}
              id="email"
              error={!!errors?.email}
              helperText={
                errors?.email
                  ? errors?.email?.type === 'required'
                    ? "  '"
                    : '   '
                  : ''
              }
              color="auxillary"
            />
            <Label htmlFor="password">*</Label>
            <InputForm
              {...register('password', {
                required: "  '",
                minLength: 8,
              })}
              type="password"
              id="password"
              error={!!errors?.password}
              helperText={
                errors?.password
                  ? errors?.email?.password === 'required'
                    ? "  '"
                    : '    8 '
                  : ''
              }
              color="auxillary"
            />
            <Label htmlFor="repeatPassword"> *</Label>
            <InputForm
              {...register('repeatPassword', {
                required: "  '",
                validate: value => value === watch('password'),
              })}
              id="repeatPassword"
              type="password"
              error={!!errors?.repeatPassword}
              helperText={
                errors?.repeatPassword
                  ? errors?.repeatPassword?.type === 'required'
                    ? "  '"
                    : '  '
                  : ''
              }
              color="auxillary"
            />
            <Button type="submit"></Button>
          </Form>
        </ThemeProvider>
      </ModalWrapper>
    </>
  );
}
